/* Typography */
h1, h2, h3 {
  color: #acacac;
  margin-top: 20px;
  margin-bottom: 10px; }

h1 {
  font-weight: light;
  font-size: 42px;
  line-height: 58px; }

h2 {
  font-weight: normal;
  font-size: 28px;
  line-height: 40px; }

h3 {
  font-weight: normal;
  font-size: 21px;
  line-height: 40px; }

h4 {
  font-weight: normal;
  font-size: 17px;
  line-height: 40px; }

h5 {
  font-weight: semibold;
  font-size: 14px;
  line-height: 40px; }

h6 {
  font-weight: semibold;
  font-size: 12px;
  line-height: 40px; }

